* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Arial', sans-serif;
  line-height: 1.6;
  color: #333;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

/* Landing Page Styles */
.landing-page {
  height: 100vh;
  background: linear-gradient(rgba(0,0,0,0.4), rgba(0,0,0,0.4)),
              url('https://images.unsplash.com/photo-1416879595882-3373a0480b5b?w=1200&h=800&fit=crop');
  background-size: cover;
  background-position: center;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: white;
}

.landing-content {
  max-width: 800px;
  padding: 40px;
  background: rgba(0,0,0,0.6);
  border-radius: 10px;
}

.company-name {
  font-size: 3.5rem;
  margin-bottom: 30px;
  color: #4CAF50;
}

.company-description {
  font-size: 1.2rem;
  margin-bottom: 40px;
  line-height: 1.8;
}

.get-started-btn {
  display: inline-block;
  padding: 15px 30px;
  background: #4CAF50;
  color: white;
  text-decoration: none;
  border-radius: 5px;
  font-size: 1.2rem;
  transition: background 0.3s;
}

.get-started-btn:hover {
  background: #45a049;
}

/* Header Styles */
.header {
  background: #2E7D32;
  color: white;
  padding: 1rem 0;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
}

.header-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  color: white;
  text-decoration: none;
}

.logo h2 {
  color: #4CAF50;
}

.navigation {
  display: flex;
  align-items: center;
}

.nav-link {
  color: white;
  text-decoration: none;
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 10px 20px;
  border-radius: 5px;
  transition: background 0.3s;
}

.nav-link:hover {
  background: rgba(255,255,255,0.1);
}

.cart-icon {
  position: relative;
  font-size: 1.5rem;
}

.cart-count {
  position: absolute;
  top: -8px;
  right: -8px;
  background: #f44336;
  color: white;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.8rem;
}

/* Product Listing Styles */
.product-listing {
  padding: 40px 0;
  background: #f5f5f5;
  min-height: 100vh;
}

.product-listing h1 {
  text-align: center;
  margin-bottom: 40px;
  color: #2E7D32;
  font-size: 2.5rem;
}

.category-section {
  margin-bottom: 50px;
}

.category-title {
  color: #2E7D32;
  margin-bottom: 30px;
  font-size: 2rem;
  border-bottom: 3px solid #4CAF50;
  padding-bottom: 10px;
}

.plants-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 30px;
}

.plant-card {
  background: white;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 6px rgba(0,0,0,0.1);
  transition: transform 0.3s, box-shadow 0.3s;
}

.plant-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 15px rgba(0,0,0,0.2);
}

.plant-image {
  width: 100%;
  height: 250px;
  object-fit: cover;
}

.plant-info {
  padding: 20px;
}

.plant-name {
  color: #2E7D32;
  margin-bottom: 10px;
  font-size: 1.3rem;
}

.plant-description {
  color: #666;
  margin-bottom: 20px;
}

.plant-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.plant-price {
  font-size: 1.5rem;
  font-weight: bold;
  color: #4CAF50;
}

.add-to-cart-btn {
  background: #4CAF50;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
  transition: background 0.3s;
}

.add-to-cart-btn:hover {
  background: #45a049;
}

/* Shopping Cart Styles */
.shopping-cart {
  padding: 40px 0;
  background: #f5f5f5;
  min-height: 100vh;
}

.shopping-cart h1 {
  text-align: center;
  margin-bottom: 40px;
  color: #2E7D32;
  font-size: 2.5rem;
}

.empty-cart {
  text-align: center;
  padding: 60px 20px;
  background: white;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0,0,0,0.1);
}

.empty-cart p {
  font-size: 1.5rem;
  color: #666;
  margin-bottom: 30px;
}

.cart-summary {
  background: white;
  padding: 30px;
  border-radius: 10px;
  margin-bottom: 30px;
  box-shadow: 0 4px 6px rgba(0,0,0,0.1);
  text-align: center;
}

.cart-summary p {
  font-size: 1.3rem;
  margin-bottom: 10px;
}

.cart-items {
  background: white;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0,0,0,0.1);
  margin-bottom: 30px;
}

.cart-item {
  display: flex;
  align-items: center;
  padding: 30px;
  border-bottom: 1px solid #eee;
  gap: 30px;
}

.cart-item:last-child {
  border-bottom: none;
}

.cart-item-image {
  width: 120px;
  height: 120px;
  object-fit: cover;
  border-radius: 10px;
}

.cart-item-details {
  flex: 1;
}

.cart-item-details h3 {
  color: #2E7D32;
  margin-bottom: 10px;
  font-size: 1.3rem;
}

.cart-item-details p {
  margin-bottom: 5px;
  font-size: 1.1rem;
}

.cart-item-controls {
  display: flex;
  flex-direction: column;
  gap: 15px;
  align-items: center;
}

.quantity-controls {
  display: flex;
  align-items: center;
  gap: 15px;
}

.quantity-btn {
  background: #4CAF50;
  color: white;
  border: none;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  cursor: pointer;
  font-size: 1.2rem;
  transition: background 0.3s;
}

.quantity-btn:hover {
  background: #45a049;
}

.quantity {
  font-size: 1.3rem;
  font-weight: bold;
  min-width: 30px;
  text-align: center;
}

.delete-btn {
  background: #f44336;
  color: white;
  border: none;
  padding: 8px 16px;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s;
}

.delete-btn:hover {
  background: #da190b;
}

.cart-actions {
  display: flex;
  justify-content: space-between;
  gap: 20px;
}

.continue-shopping-btn, .checkout-btn {
  flex: 1;
  padding: 15px 30px;
  border-radius: 5px;
  font-size: 1.2rem;
  text-decoration: none;
  text-align: center;
  cursor: pointer;
  transition: all 0.3s;
  border: none;
}

.continue-shopping-btn {
  background: #2196F3;
  color: white;
  display: block;
}

.continue-shopping-btn:hover {
  background: #1976D2;
}

.checkout-btn {
  background: #4CAF50;
  color: white;
}

.checkout-btn:hover {
  background: #45a049;
}

/* Responsive Design */
@media (max-width: 768px) {
  .company-name {
    font-size: 2.5rem;
  }
  
  .company-description {
    font-size: 1rem;
  }
  
  .header-content {
    flex-direction: column;
    gap: 20px;
  }
  
  .plants-grid {
    grid-template-columns: 1fr;
  }
  
  .cart-item {
    flex-direction: column;
    text-align: center;
  }
  
  .cart-actions {
    flex-direction: column;
  }
}
